import Endpoint from '@/services/Endpoint'

export default {
  cardBrands (payload) {
    return Endpoint().get('/s1/card/cardBrand', {
      params: payload
    })
  },
  createCardBrand (payload) {
    return Endpoint().post('/s1/card/cardBrand', payload)
  },
  modifyCardBrand (payload) {
    return Endpoint().patch('/s1/card/cardBrand', payload)
  }
}
